/* ------------------------------------------------------------------------------------------------ */
/* General */
/* ------------------------------------------------------------------------------------------------ */

#wrap {
  width: 100%;
  overflow-x: hidden; }

@include mobile {
  .container {
    padding-left: 30px;
    padding-right: 30px; } }

.btn {
  font-size: 10px;
  line-height: 20px;
  font-weight: 700;
  padding: 10px 24px;
  border-radius: 5px;
  border: 0;
  text-transform: uppercase;
  &-primary {
    background: transparent linear-gradient(180deg, #6BBB71 0%, #017961 100%) 0% 0% no-repeat padding-box;
    box-shadow: 0px 3px 6px #00000029;
    &:hover {
      background: transparent linear-gradient(180deg, #017961 0%, #017961 100%) 0% 0% no-repeat padding-box; }
    &:focus,
    &:active,
    &:active:focus {
      background: transparent linear-gradient(180deg, #017961 0%, #017961 100%) 0% 0% no-repeat padding-box;
      box-shadow: 0px 3px 6px #00000029 !important;
      outline: none; } }
  &-secondary {
    background: transparent linear-gradient(178deg, #81C4FF 0%, #0D6AB7 100%) 0% 0% no-repeat padding-box;
    box-shadow: 0px 3px 6px #00000029;
    &:hover {
      background: transparent linear-gradient(178deg, #81C4FF 0%, #0D6AB7 100%) 0% 0% no-repeat padding-box; }
    &:focus,
    &:active,
    &:active:focus {
      background: transparent linear-gradient(178deg, #81C4FF 0%, #0D6AB7 100%) 0% 0% no-repeat padding-box;
      box-shadow: 0px 3px 6px #00000029 !important;
      outline: none; } } }

.btn-detail {
  white-space: nowrap;
  img,
  .svg {
    position: relative;
    top: -1px;
    height: 16px;
    width: auto;
    display: inline-block;
    margin-left: 26px;
    path {
      transition: all .3s ease-in-out; } }
  &:hover {
    .svg path {
      fill: $primary; } } }

.badge {
  font-size: 12px;
  line-height: 18px;
  font-weight: 700;
  border-radius: 5px;
  padding: 8.5px 15px;
  min-width: 92px;
  &-primary {
    color: #FFFFFF;
    background: transparent linear-gradient(180deg, #6BBB71 0%, #017961 100%) 0% 0% no-repeat padding-box; }
  &-secondary {
    color: #FFFFFF;
    background: transparent linear-gradient(178deg, #81C4FF 0%, #0D6AB7 100%) 0% 0% no-repeat padding-box; }
  &-warning {
    color: #FFFFFF;
    background: transparent linear-gradient(180deg, #FFDD80 0%, #FFBB00 100%) 0% 0% no-repeat padding-box; }
  &-block {
    width: 100%; } }


.accordion {
  .acc {
    position: relative;
    background: transparent;
    border-radius: 0;
    border: 0;
    border-bottom: 1px solid #707070;
    margin-bottom: 40px;
    .moreless {
      position: absolute;
      z-index: 2;
      right: 0;
      bottom: 12px;
      font-size: 12px;
      line-height: 1.5em;
      color: #70707090;
      text-transform: uppercase;
      cursor: pointer;
      transition: all .3s linear;
      &:hover {
        color: #707070; } }
    &-header {
      position: relative;
      background: transparent;
      border: 0; }
    &-title {
      display: block;
      position: relative;
      padding: 0 0 0 38px;
      margin: 0;
      transition: all .3s linear;
      cursor: pointer;
      color: $primary;
      font-size: 40px;
      line-height: 1.5em;
      letter-spacing: 0.42px;
      font-weight: 700;
      span.num {
        font-size: 15px;
        line-height: 1.5em;
        display: block;
        position: absolute;
        top: 12px;
        left: 0;
        width: 32px; }
      &.collapsed {
        padding-bottom: 40px;
        color: $text; } }
    &-body {
      padding: 12px 0 40px 40px;
      p {
        font-size: 14px;
        line-height: 20px;
        letter-spacing: 0.14px; } } }
  @include mobile {
    .acc {
      margin-bottom: 24px;
      .moreless {
        &.collapsed {
          bottom: 28px; } }
      &-title {
        font-size: 20px;
        line-height: 30px;
        padding: 0 0 0 20px;
        span.num {
          font-size: 10px;
          line-height: 1.5em;
          top: 4px; }
        &.collapsed {
          padding-bottom: 24px; } }
      &-body {
        padding: 12px 0 40px 20px; } } } }




.table {
  font-size: 12px;
  line-height: 18px;
  font-weight: 700;
  td, th {
    padding: 20px 30px;
    border: 0;
    vertical-align: middle; }

  thead th {
    border-bottom: 1px solid #707070; } }



.table-striped {
  tbody {
    tr:nth-of-type(odd) {
      background: #FFFFFF; }
    tr:nth-of-type(even) {
      background: #F3F3F3; } } }


.loading-page {
  position: fixed;
  z-index: 1000;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background: #FFF;
  display: flex;
  align-items: center;
  text-align: center;
  // padding-bottom: 140px
  &__wrap {
    width: 100%; }
  &__logo {
    img {
      display: block;
      height: 106px;
      width: auto; }
    .icon {
      display: inline-block;
      img {
        @include logoRotation(); } }
    .text {
      display: inline-block;
      margin-left: 30px; } }
  .foot-text {
    display: block;
    width: 100%;
    position: absolute;
    left: 0;
    bottom: 70px;
    font-size: 12px;
    line-height: 18px;
    font-weight: 700;
    text-align: center; }
  @include mobile {
    &__logo {
      img {
        height: 40px; }
      .text {
        margin-left: 8px; } }
    .foot-text {
      font-size: 10px;
      line-height: 18px;
      bottom: 80px; } } }


.btn-joinus {
  display: block;
  z-index: 10;
  position: fixed;
  right: -100%;
  bottom: 30px;
  color: #FFF;
  font-family: "Druk Wide", sans-serif;
  font-size: 10px;
  line-height: 18px;
  font-weight: 700;
  text-transform: uppercase;
  padding: 45px 28px;
  background: {}
  background: transparent linear-gradient(180deg, #6BBB71 0%, #017961 100%) 0% 0% no-repeat padding-box;
  border-radius: 5px 0 0 5px;
  cursor: pointer;
  transition: all 0.5s ease;
  transition-delay: 1s;
  @include mobile {
    display: none; } }

.floating-number {
  position: absolute;
  top: -7.5%;
  right: -7.5%;
  width: 15px;
  font-size: 10px;
  line-height: 15px;
  display: block;
  background: #DF5055;
  border-radius: 3px;
  text-align: center;
  color: #fff; }

.link-seemore {
  a {
    font-size: 12px;
    line-height: 16px;
    position: relative;
    vertical-align: middle;
    display: blocka;
    color: #4C9F70;
    &:after {
      content: "";
      display: inline-block;
      vertical-align: middle;
      width: 16px;
      height: 16px;
      background-color: #4C9F70;
      background-image: url(../images/ic-arrow-right-white.svg);
      background-size: 6px 5px;
      background-repeat: no-repeat;
      background-position: center;
      border-radius: 5px;
      margin-left: 8px; } } }

.block-dashboard {
  .row {
    margin-left: -8px;
    margin-right: -8px;
    [class*="col-"] {
      padding-left: 8px;
      padding-right: 8px; } }
  .box-card {
    padding: 25px;
    border-radius: 10px;
    box-shadow: 0px 3px 6px #00000029;
    background: #fff;
    position: relative;
    height: 100%;
    // margin-bottom: 20px
    .icon {
      width: 44px;
      height: 44px;
      background: #cecece;
      display: flex;
      margin-bottom: 21px;
      border-radius: 7px;
      position: relative;
      .ic {
        width: 100%;
        height: 100%;
        background-size: contain;
        background-repeat: no-repeat;
        background-position: center;
        border-radius: 7px; } }
    .ext-link {
      width: 21px;
      height: 21px;
      display: inline-block;
      position: absolute;
      top: 30px;
      right: 30px;
      background-image: url(../images/ic-arrow-right-grey.svg);
      background-size: contain;
      background-repeat: no-repeat;
      background-position: center;
      transform: rotate(-45deg); }
    h3 {
      font-family: "Druk Wide", sans-serif;
      font-size: 20px;
      line-height: 30px;
      margin-bottom: 30px; }
    h4 {
      font-size: 18px;
      line-height: 20px;
      margin-bottom: 10px; }
    p {
      font-size: 12px;
      line-height: 16px; }
    .contact,
    .text {
      font-size: 12px;
      line-height: 16px;
      a {
        display: inline-block; }
      p {
        margin-bottom: 0; }
      &:not(:last-child) {
        margin-bottom: 21px; } }
    .total {
      span {
        font-size: 24px;
        line-height: 29px;
        font-weight: bold; } }

    &.gradient-green {
      background: transparent linear-gradient(130deg, #96d177 0%, #1f9e6f 100%) 0% 0% no-repeat padding-box;
      h3,
      p {
        color: #fff; }
      .ext-link {
        background-image: url(../images/ic-arrow-right-white.svg); } }
    &.cream {
      background: #FFF3D9;
      h4 {
        color: #FFBB00; }
      .link-seemore {
        a {
          color: #FFBB00;
          &:after {
            background-color: #FFBB00; } } } }

    &.light-green {
      background: #EFF8F1;
      h4 {
        color: #4C9F70; }
      .link-seemore {
        a {
          color: #4C9F70;
          &:after {
            background-color: #4C9F70; } } } } }

  .box-po {
    padding-right: 41px;
    .small-title {
      font-size: 12px;
      line-height: 22px;
      color: #777; }
    h3 {
      font-family: "Druk Wide", sans-serif;
      font-weight: bold;
      font-size: 20px;
      line-height: 24px;
      margin-bottom: 8px; }
    .progress-bar {
      background: transparent;
      margin-bottom: 30px;
      .pb {
        &-block {
          display: inline-block;
          width: calc(100% - 43px);
          height: 8px;
          background: #E8E8E8;
          border-radius: 10px;
          position: relative;
          overflow: hidden;
          span {
            position: absolute;
            height: 8px;
            top: 0;
            left: 0;
            background: #4C9F70;
            border-radius: 10px; } }
        .prcntg {
          display: inline-block;
          font-size: 16px;
          line-height: 22px;
          font-weight: bold;
          margin-left: 8px;
          color: #4C9F70; } }
      small {
        font-size: 10px;
        line-height: 14px;
        color: #000000;
        text-align: left; } }
    .box-text {
      p {
        font-size: 24px;
        line-height: 29px;
        font-weight: bold;
        margin-bottom: 3px; }
      small {
        font-size: 12px;
        line-height: 22px;
        color: #707070; } } } }

.pagination-wrap {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  margin-top: 21px;
  u {
    display: inline-block;
    font-size: 20px;
    line-height: 24px;
    margin-right: 52px;
    color: #555555; }
  .pagination {
    margin-left: -4px;
    margin-right: -4px;
    li {
      display: inline-block;
      vertical-align: middle;
      padding: 0 4px;
      a,
      span {
        color: #85878A;
        display: block;
        width: 32px;
        font-size: 14px;
        line-height: 32px;
        text-align: center;
        border-radius: 5px; }
      &.disable {
        a,
        span {
          opacity: .4;
          pointer-event: none; } }
      &.active,
      &:hover {
        a,
        span {
          color: #fff;
          background: #6BBB71; } }
      &.before,
      &.after {
        span,
        a {
          width: 76px;
          height: 32px;
          display: flex;
          background: #E8E8E8;
          border-radius: 5px;
          text-align: center;
          transition: all .5s ease;
          .ic-arrow {
            width: 16px;
            height: 14px;
            display: inline-block;
            margin: auto;
            background: url(../images/ic-arrow-right-grey.svg);
            background-size: contain;
            background-repeat: no-repeat;
            background-position: center; } }
        &:hover {
          a,
          span {
            background-color: #6BBB71;
            .ic-arrow {
              background: url(../images/ic-arrow-right-white.svg);
              background-size: contain;
              background-repeat: no-repeat;
              background-position: center; } } } }
      &.before {
        span,
        a {
          .ic-arrow {
            transform: rotate(-180deg); } } } } } }

.section__head {
  margin-bottom: 30px;
  h2 {
    font-size: 38px;
    line-height: 46px;
    font-weight: bold;
    margin-bottom: 8px; }
  p {
    font-size: 16px;
    line-height: 22px;
    margin-bottom: 20px; } }

.mt-70 {
  margin-top: 70px;
  @include maxtablet {
    margin-top: 35px; } }
