/* ------------------------------------------------------------------------------------------------ */
/* Header */
/* ------------------------------------------------------------------------------------------------ */

.header {
  z-index: 100;
  position: fixed;
  width: 100%;
  padding: 28px 0;
  transition: all .3s ease-in-out;
  background: #fff;
  box-shadow: 0px 3px 6px #00000029;
  .container {
    display: flex;
    align-items: center; }
  &__logo {
    position: relative;
    display: block;
    img {
      display: block;
      height: 32px;
      width: auto; }
    .img {
      display: block; }
    .img-alt {
      display: none; }
    .text {
      display: block;
      position: absolute;
      color: $text;
      font-size: 12px;
      line-height: 18px;
      text-transform: uppercase;
      bottom: 2px;
      left: calc(100% + 0.5rem); } }

  &__menu {
    display: block;
    margin-left: auto;
    ul {
      display: flex;
      width: 100%;
      &> li {
        display: block;
        a {
          color: $text;
          display: block;
          font-size: 12px;
          line-height: 18px;
          font-weight: 400;
          padding: 11px 32px;
          opacity: 1;
          transition: all .3s ease-in-out;
          &:hover {
            color: $text;
            opacity: 0.5; } }
        &.btn-home,
        &.btn-notif {
          border-right: 1px solid #CACACA;
          .icon {
            width: 28px;
            height: 28px;
            display: inline-block;
            background: #cecece;
            position: relative;
            .ic {
              width: 100%;
              height: 100%;
              background-size: contain;
              background-repeat: no-repeat;
              background-position: center; }
            .number {
              width: 15px;
              text-align: center;
              font-size: 10px;
              line-height: 15px;
              color: #fff;
              display: inline-block;
              letter-spacing: 0.25px;
              background: #DF5055;
              border-radius: 3px;
              position: absolute;
              top: -10px;
              right: -12px;
              font-style: normal; } } }
        &.btn-notif {
          margin-right: 32px; }
        &.btn-login {
          margin-left: 13px; }
        &.btn-login a {
          color: #FFF;
          font-family: "Druk Wide", sans-serif;
          font-weight: 600;
          border-radius: 5px;
          background: $primary linear-gradient(180deg, rgba(107, 187, 113, 1) 0%, rgba(1, 121, 97, 1) 100%) 0% 0% no-repeat padding-box;
          box-shadow: 0px 3px 6px rgba(0, 0, 0, 0.16);
          &:hover {
            opacity: 1;
            background: darken($primary, 5%);
            background: $primary linear-gradient(180deg, rgba(1, 121, 97, 1) 0%, rgba(1, 121, 97, 1) 100%) 0% 0% no-repeat padding-box; } } } } }

  &.header--white {
    .header__logo {
      .img {
        display: none; }
      .img-alt {
        display: block; }
      .text {
        color: #FFFFFF; } }
    .header__menu {
      ul > li:not(.btn-login) a {
        color: #FFFFFF;
        &:hover {
          color: #FFFFFF; } } } }

  &.sticky {
    background: #FFFFFF;
    padding: 14px 0;
    box-shadow: 0px 3px 6px rgba(0, 0, 0, 0.1);
    .header__logo {
      .img {
        display: block; }
      .img-alt {
        display: none; } }
    .header__menu {
      ul > li:not(.btn-login) a {
        color: $text;
        &:hover {
          color: $text; } } } } }


@include mobile {
  .header {
    background: #FFFFFF;
    padding: 42px 0;
    &__logo {
      position: relative;
      z-index: 2;
      img {
        height: 26px; } }
    &__menu {
      position: fixed;
      width: 100%;
      height: 100%;
      top: 0;
      left: -100%;
      background: #FFFFFF;
      padding-top: 110px;
      opacity: 0;
      transition: all .3s ease-in-out;
      ul {
        display: block;
        padding: 30px 30px;
        li {
          display: block;
          padding: 12px 0;
          text-align: left;
          a {
            display: inline-block;
            padding: 0; }
          &.btn-login {
            a {
              padding: 11px 34px; } } } } }

    &__toggle {
      width: 23px;
      height: 13px;
      margin: 10px 15px 10px auto;
      position: relative;
      z-index: 210;
      top: 0;
      transition: all .5s ease-in-out;
      span {
        display: block;
        height: 3px;
        width: 100%;
        border-radius: 1px;
        background: #000000;
        position: absolute;
        left: 0;
        border-radius: 2px;
        opacity: 1;
        transform: none;
        transition: all .3s ease-in-out;
        &:nth-child(1) {
          top: 0; }
        &:nth-child(2) {
          top: 5px; }
        &:nth-child(3) {
          top: 10px; } } }


    &.sticky {
      padding: 20px 0; } }

  body.menu-open {
    overflow: hidden;
    .header {
      padding: 42px 0;
      &__menu {
        opacity: 1;
        left: 0; }
      &__toggle {
        transition: all .5s ease-in-out;
        span {
          &:nth-child(1) {
            top: 5px;
            transform: rotate(45deg); }
          &:nth-child(2) {
            top: 5px;
            opacity: 0; }
          &:nth-child(3) {
            top: 5px;
            transform: rotate(135deg); } } } } } }
